:root {
  --default-color-link: #000;
  --color-active-link: #00acdc;
  --background-header: #fff;
  --box-shadow: 0 2px 4px 0 #0000000d;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  background: var(--background-header);
  box-shadow: var(--box-shadow);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

.nav {
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
  display: flex;
}

.nav-item:last-child {
  margin-right: 0;
}

.nav-link {
  color: var(--default-color-link);
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  padding: 23px 0;
  font-size: 12px;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--color-active-link);
}

.is-active {
  color: var(--color-active-link);
  position: relative;
}

.is-active:after {
  background: var(--color-active-link);
  content: "";
  border-radius: 8px;
  flex-shrink: 0;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

